body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.counter {
    font-size: 20px;
    background-color: #45cbbb;
    padding: 10px;
    margin-top: 10px;
    color: blue;
    font-weight: bold;
}

.header {
    background-color: green;
    padding: 20px 10px;
    color: whitesmoke;
    text-transform: uppercase;
}
.animation {
    height: 100px;
    position: relative;
}

.animation__circle {
    position: absolute;
    top: 10px;
    left: 0;
    background-color: black;
    width: 80px;
    height: 80px;
    border-radius: 50%;
    -webkit-animation: move 5s linear infinite alternate, mirror 10s step-end infinite;
            animation: move 5s linear infinite alternate, mirror 10s step-end infinite;
}

.animation__circle::before {
    content: '';
    position: absolute;
    top: 5px;
    right: 30px;
    width: 15px;
    height: 15px;
    border-radius: 50%;
    background-color: white;
    -webkit-animation: eyemove .25s linear infinite alternate;
            animation: eyemove .25s linear infinite alternate;
}

.animation__circle::after {
    content: '';
    position: absolute;
    right: 0;
    top: 50%;
    border-style: solid;
    border-color: transparent;
    border-width: 30px;
    border-right-style: solid;
    border-right-color: white;
    border-right-width: 40px;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    -webkit-animation: eat .25s linear infinite alternate;
            animation: eat .25s linear infinite alternate;
}

@-webkit-keyframes move {
    from {
        left: 0;
    }
  
    to {
        left: calc(100% - 80px);
    }
}

@keyframes move {
    from {
        left: 0;
    }
  
    to {
        left: calc(100% - 80px);
    }
}

@-webkit-keyframes eat {
    from {
        border-width: 30px;
        border-right-width: 40px;
    }
  
    to {
        border-width: 0;
        border-right-width: 40px;
    }
}

@keyframes eat {
    from {
        border-width: 30px;
        border-right-width: 40px;
    }
  
    to {
        border-width: 0;
        border-right-width: 40px;
    }
}

@-webkit-keyframes mirror {
    from {
        -webkit-transform: scale(1, 1);
                transform: scale(1, 1);
    }

    50% {
        -webkit-transform: scale(-1, 1);
                transform: scale(-1, 1);
    }

    to {
        -webkit-transform: scale(-1, 1);
                transform: scale(-1, 1);
    }
}

@keyframes mirror {
    from {
        -webkit-transform: scale(1, 1);
                transform: scale(1, 1);
    }

    50% {
        -webkit-transform: scale(-1, 1);
                transform: scale(-1, 1);
    }

    to {
        -webkit-transform: scale(-1, 1);
                transform: scale(-1, 1);
    }
}

@-webkit-keyframes eyemove {
    from {
        top: 5px;
        right: 30px;
    }
  
    to {
        top: 15px;
        right: 20px;
    }
}

@keyframes eyemove {
    from {
        top: 5px;
        right: 30px;
    }
  
    to {
        top: 15px;
        right: 20px;
    }
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

